import { useState } from 'react';
import {
  AppBar,
  Box,
  Button,
  Container,
  Drawer,
  IconButton,
  List,
  ListItem,
  ListItemText,
  Toolbar,
  Typography,
} from '@mui/material';
import { NavLink, useLocation } from 'react-router-dom';
import MenuIcon from '@mui/icons-material/Menu';
import '../css/App.css'; // Import the CSS file

const NavBar = function () {
  const [drawerOpen, setDrawerOpen] = useState(false);
  const location = useLocation();

  const toggleDrawer = () => {
    setDrawerOpen(!drawerOpen);
  };

  const getPageTitle = () => {
    switch (location.pathname) {
      case '/':
        return 'Home';
      case '/cv':
        return 'CV';
      case '/work-experience':
        return 'Timeline';
      case '/projects':
        return 'Projects';
      default:
        return '';
    }
  };

  const drawer = (
    <Box onClick={toggleDrawer} onKeyDown={toggleDrawer} sx={{ width: 250 }}>
      <List>
        <ListItem button component={NavLink} to="/" exact>
          <ListItemText primary="Home" />
        </ListItem>
        <ListItem button component={NavLink} to="/cv">
          <ListItemText primary="CV" />
        </ListItem>
        <ListItem button component={NavLink} to="/work-experience">
          <ListItemText primary="Timeline" />
        </ListItem>
        <ListItem button component={NavLink} to="/projects">
          <ListItemText primary="Projects" />
        </ListItem>
      </List>
    </Box>
  );

  return (
    <>
      <Box
        position="fixed"
        top={0}
        width="100%"
        bgcolor="background.paper"
        zIndex={1000}
        py={2}
        sx={{ boxShadow: 'none', display: { xs: 'none', sm: 'block' } }}
      >
        <Container>
          <Box display="flex" justifyContent="center">
            <Box>
              <Button
                component={NavLink}
                to="/"
                exact="true"
                className="nav-link"
                sx={{
                  margin: '0 10px',
                  textDecoration: 'none',
                  color: location.pathname === '/' ? 'primary.main' : 'inherit',
                  bgcolor:
                    location.pathname === '/' ? 'primary.light' : 'transparent',
                }}
              >
                Home
              </Button>
              <Button
                component={NavLink}
                to="/cv"
                className="nav-link"
                sx={{
                  margin: '0 10px',
                  textDecoration: 'none',
                  color:
                    location.pathname === '/cv' ? 'primary.main' : 'inherit',
                  bgcolor:
                    location.pathname === '/cv'
                      ? 'primary.light'
                      : 'transparent',
                }}
              >
                CV
              </Button>
              <Button
                component={NavLink}
                to="/work-experience"
                className="nav-link"
                sx={{
                  margin: '0 10px',
                  textDecoration: 'none',
                  color:
                    location.pathname === '/work-experience'
                      ? 'primary.main'
                      : 'inherit',
                  bgcolor:
                    location.pathname === '/work-experience'
                      ? 'primary.light'
                      : 'transparent',
                }}
              >
                Timeline
              </Button>
              <Button
                component={NavLink}
                to="/projects"
                className="nav-link"
                sx={{
                  margin: '0 10px',
                  textDecoration: 'none',
                  color:
                    location.pathname === '/projects'
                      ? 'primary.main'
                      : 'inherit',
                  bgcolor:
                    location.pathname === '/projects'
                      ? 'primary.light'
                      : 'transparent',
                }}
              >
                Projects
              </Button>
            </Box>
          </Box>
        </Container>
      </Box>
      <AppBar position="fixed" sx={{ display: { xs: 'block', sm: 'none' } }}>
        <Toolbar sx={{ justifyContent: 'space-between' }}>
          <Box sx={{ display: 'flex', alignItems: 'center' }}>
            <IconButton
              edge="start"
              color="inherit"
              aria-label="menu"
              onClick={toggleDrawer}
              sx={{
                display: { xs: 'block', sm: 'none' },
              }}
            >
              <MenuIcon />
            </IconButton>
            <Typography
              variant="h6"
              component="div"
              sx={{ flexGrow: 1, textAlign: 'center' }}
            >
              {getPageTitle()}
            </Typography>
          </Box>
          <Drawer
            anchor="left"
            open={drawerOpen}
            onClose={toggleDrawer}
            sx={{ display: { xs: 'block', md: 'none' } }}
          >
            {drawer}
          </Drawer>
        </Toolbar>
      </AppBar>
    </>
  );
};

export default NavBar;
