import React, { useEffect, useState } from 'react';
import { Helmet } from 'react-helmet';
import {
  Box,
  Container,
  Divider,
  Grid,
  Link as MuiLink,
  List,
  ListItem,
  ListItemText,
  Typography,
  useMediaQuery,
  useTheme,
} from '@mui/material';
import GitHubIcon from '@mui/icons-material/GitHub';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import SchoolIcon from '@mui/icons-material/School';
import PageContainer from '../components/PageContainer';
import info from '../data/personalDetails';
import seoData from '../data/seoData';

const CV = function () {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));

  const { title, description, url, image, keywords } = seoData.cv;

  const [fadeIn, setFadeIn] = useState(false);

  useEffect(() => {
    // Trigger fade-in animation on mount
    setFadeIn(true);
  }, []);

  return (
    <PageContainer title="CV">
      <Helmet>
        <title>{title}</title>
        <meta name="description" content={description} />
        <meta property="og:title" content={title} />
        <meta property="og:description" content={description} />
        <meta property="og:url" content={url} />
        <meta property="og:image" content={image} />
        <meta name="twitter:card" content="summary_large_image" />
        <meta name="twitter:title" content={title} />
        <meta name="twitter:description" content={description} />
        <meta name="twitter:image" content={image} />
        <meta name="keywords" content={keywords} />
        <script type="application/ld+json">
          {JSON.stringify({
            "@context": "https://schema.org",
            "@type": "Person",
            "name": info.name,
            "jobTitle": info.jobTitle,
            "alumniOf": info.education.map(edu => edu.institution),
            "sameAs": [
              info.contact_info.linkedinUrl,
              info.contact_info.githubUrl,
              info.contact_info.scholarUrl
            ]
          })}
        </script>
      </Helmet>
      <Container justifyContent="center"
        sx={{
          opacity: fadeIn ? 1 : 0,
          transition: 'opacity 0.5s',
        }}
        className={fadeIn ? 'fadeInAnimation' : ''} >
        <Box mb={4}>
          <Typography variant="h5" gutterBottom>
            Work Experience
          </Typography>
          {info.workExperiences.map((job, id) => (
            <Box key={id} mb={2}>
              <Grid container justifyContent="space-between">
                <Grid item xs={12} sm={8}>
                  <Typography variant="h6">{job.title}</Typography>
                  {isMobile && (
                    <Typography
                      variant="body2"
                      color="textSecondary"
                      align="left"
                    >
                      {job.start} - {job.end}
                    </Typography>
                  )}
                  <Typography variant="subtitle1" color="textSecondary">
                    {job.company}
                  </Typography>
                  {isMobile && (
                    <Typography
                      variant="body2"
                      color="textSecondary"
                      align="left"
                    >
                      {job.location}
                    </Typography>
                  )}
                  <Typography variant="body1" color="textPrimary">
                    {job.shortDescription}
                  </Typography>
                  <List sx={{ pl: 2 }}>
                    {job.description.map((desc, i) => (
                      <ListItem
                        key={i}
                        disableGutters
                        sx={{ mb: 0, pl: 1, padding: 0 }}
                      >
                        <ListItemText primary={`• ${desc}`} />
                      </ListItem>
                    ))}
                  </List>
                </Grid>
                {!isMobile && (
                  <Grid item>
                    <Typography
                      variant="body2"
                      color="textSecondary"
                      align="right"
                    >
                      {job.start} - {job.end}
                    </Typography>
                    <Typography
                      variant="body2"
                      color="textSecondary"
                      align="right"
                    >
                      {job.location}
                    </Typography>
                  </Grid>
                )}
              </Grid>
              <Divider sx={{ my: 2 }} />
            </Box>
          ))}
        </Box>

        <Box mb={4}>
          <Typography variant="h5" gutterBottom>
            Education
          </Typography>
          {info.education.map((edu, id) => (
            <Box key={id} mb={2}>
              <Grid container justifyContent="space-between">
                <Grid item xs={12} sm={8}>
                  <Typography variant="h6">{edu.title}</Typography>
                  {isMobile && (
                    <Typography
                      variant="body2"
                      color="textSecondary"
                      align="left"
                    >
                      {edu.start} - {edu.end}
                    </Typography>
                  )}
                  <Typography variant="subtitle1" color="textSecondary">
                    {edu.institution}
                  </Typography>
                  {isMobile && (
                    <Typography
                      variant="body2"
                      color="textSecondary"
                      align="left"
                    >
                      {edu.location}
                    </Typography>
                  )}
                  <Typography variant="body1" color="textPrimary">
                    {edu.shortDescription}
                  </Typography>
                  <List sx={{ pl: 2 }}>
                    {edu.description.map((desc, i) => (
                      <ListItem
                        key={i}
                        disableGutters
                        sx={{ mb: 0, pl: 1, padding: 0 }}
                      >
                        <ListItemText primary={`• ${desc}`} />
                      </ListItem>
                    ))}
                  </List>
                </Grid>
                {!isMobile && (
                  <Grid item>
                    <Typography
                      variant="body2"
                      color="textSecondary"
                      align="right"
                    >
                      {edu.start} - {edu.end}
                    </Typography>
                    <Typography
                      variant="body2"
                      color="textSecondary"
                      align="right"
                    >
                      {edu.location}
                    </Typography>
                  </Grid>
                )}
              </Grid>
              <Divider sx={{ my: 2 }} />
            </Box>
          ))}
        </Box>

        <Box mb={4}>
          <Typography variant="h5" gutterBottom>
            Languages
          </Typography>
          {info.languages.map((lang, id) => (
            <Box key={id}>
              <Grid container justifyContent="space-between">
                <Grid item>
                  <Typography variant="body1">{lang.name}</Typography>
                </Grid>

                <Grid item>
                  <Typography variant="body1">{lang.proficiency}</Typography>
                </Grid>
              </Grid>
            </Box>
          ))}
        </Box>

        <Box mb={4}>
          <Typography variant="h5" gutterBottom>
            Skills
          </Typography>
          <Typography variant="body1">{info.skills.join(', ')}</Typography>
        </Box>

        <Box mb={4}>
          <Typography variant="h5" gutterBottom>
            Hobbies
          </Typography>
          <Typography variant="body1">{info.hobbies.join(', ')}</Typography>
        </Box>

        <Box mb={4}>
          <Typography variant="h5" gutterBottom>
            Contact
          </Typography>
          <Typography variant="body1">
            Address: {info.contact_info.location}
          </Typography>
          Follow me:{' '}
          <MuiLink
            href={info.contact_info.linkedinUrl}
            target="_blank"
            rel="noopener noreferrer"
          >
            <LinkedInIcon>{info.contact_info.linkedinUrl}</LinkedInIcon>
          </MuiLink>
          <MuiLink
            href={info.contact_info.githubUrl}
            target="_blank"
            rel="noopener noreferrer"
          >
            <GitHubIcon>{info.contact_info.githubUrl}</GitHubIcon>
          </MuiLink>
          {info.contact_info.scholarUrl ? (
            <MuiLink
              href={info.contact_info.scholarUrl}
              target="_blank"
              rel="noopener noreferrer"
            >
              <SchoolIcon>{info.contact_info.scholarUrl}</SchoolIcon>
            </MuiLink>
          ) : (
            ''
          )}
        </Box>
      </Container>
    </PageContainer>
  );
};

export default CV;
