import { Container, Typography } from '@mui/material';
import { Helmet } from 'react-helmet';
import {
  Timeline,
  TimelineItem,
  TimelineSeparator,
  TimelineDot,
  TimelineConnector,
  TimelineContent,
  TimelineOppositeContent,
} from '@mui/lab';
import { motion } from 'framer-motion';
import PageContainer from '../components/PageContainer';
import '../css/App.css';
import '../css/Animation.css';
import info from '../data/personalDetails';
import seoData from '../data/seoData';

const mergedEvents = [...info.workExperiences, ...info.education];
const sortedEvents = mergedEvents.sort((a, b) => {
  let [monthA, yearA] = a.start.split('.').map(Number);
  let [monthB, yearB] = b.start.split('.').map(Number);

  if (!yearA) {
    yearA = monthA
  }
  if (!yearB) {
    yearB = monthB
  }
  return yearB - yearA || monthB - monthA;
});

const TimelineEvents = function () {
  const { title, description, url, image, keywords } = seoData.timeline;

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        delayChildren: 0.3,
        staggerChildren: 0.2,
      },
    },
  };

  const itemVariants = {
    hidden: { opacity: 0, y: 20 },
    visible: { opacity: 1, y: 0 },
  };

  return (
    <PageContainer title="Timeline">
      <Helmet>
        <title>{title}</title>
        <meta name="description" content={description} />
        <meta property="og:title" content={title} />
        <meta property="og:description" content={description} />
        <meta property="og:url" content={url} />
        <meta property="og:image" content={image} />
        <meta name="twitter:card" content="summary_large_image" />
        <meta name="twitter:title" content={title} />
        <meta name="twitter:description" content={description} />
        <meta name="twitter:image" content={image} />
        <meta name="keywords" content={keywords} />
      </Helmet>
      <Container>
        <motion.div
          variants={containerVariants}
          initial="hidden"
          animate="visible"
        >
          <Timeline position="alternate">
            {sortedEvents.map((experience, index) => (
              <TimelineItem key={index}>
                <TimelineOppositeContent color="text.secondary">
                  {experience.start} - {experience.end}
                </TimelineOppositeContent>
                <TimelineSeparator>
                  <TimelineDot />
                  {index < sortedEvents.length - 1 && <TimelineConnector />}
                </TimelineSeparator>
                <TimelineContent>
                  <motion.div variants={itemVariants}>
                    <Typography variant="h6">{experience.title}</Typography>
                    {experience.company ? (
                      <Typography variant="body1">
                        {experience.company}
                      </Typography>
                    ) : ''}
                    {experience.institution ? (
                      <Typography variant="body1">
                        {experience.institution}
                      </Typography>
                    ) : ''}
                    <Typography variant="body2">
                      {experience.shortDescription}
                    </Typography>
                  </motion.div>
                </TimelineContent>
              </TimelineItem>
            ))}
          </Timeline>
        </motion.div>
      </Container>
    </PageContainer>
  );
};

export default TimelineEvents;
