import React, { useEffect, useState } from 'react';
import { Helmet } from 'react-helmet';
import { Avatar, Box, Typography } from '@mui/material';
import '../css/App.css';
import '../css/Animation.css';
import info from '../data/personalDetails';
import seoData from '../data/seoData';

const Home = function ({ isHomePage }) {
  const [fadeIn, setFadeIn] = useState(false);
  const { title, description, url, image, keywords } = seoData.home;

  useEffect(() => {
    // Trigger fade-in animation on mount
    setFadeIn(true);
  }, []);

  return (
    <Box
      display="flex"
      justifyContent="center"
      alignItems="center"
      minHeight="100vh"
      overflow="hidden"
      flexDirection="column"
      sx={{
        mt: isHomePage ? 0 : 10,
        opacity: fadeIn ? 1 : 0,
        transition: 'opacity 0.5s',
      }}
      className={fadeIn ? 'fadeInAnimation' : ''}
    >
      <Helmet>
        <title>{title}</title>
        <meta name="description" content={description} />
        <meta property="og:title" content={title} />
        <meta property="og:description" content={description} />
        <meta property="og:url" content={url} />
        <meta property="og:image" content={image} />
        <meta name="twitter:card" content="summary_large_image" />
        <meta name="twitter:title" content={title} />
        <meta name="twitter:description" content={description} />
        <meta name="twitter:image" content={image} />
        <meta name="keywords" content={keywords} />
      </Helmet>
      <Avatar
        alt={`${info.first_name} ${info.last_name}`}
        src="/assets/profile.jpeg"
        // sx={{ width: 200, height: 200, marginBottom: 2 }}
        sx={{ width: 250, height: 250, marginBottom: 2 }}
      />
      <Typography
        variant="h2"
        component="h1"
        gutterBottom
        sx={{ textAlign: 'center', justifyContent: 'center' }}
      >
        {info.first_name} {info.last_name}
      </Typography>
      <Typography variant="h5" component="h2" align="center">
        {info.current_work_title}
      </Typography>
      <Typography variant="h6" component="h2">
        {info.about_me}
      </Typography>
    </Box>
  );
};

export default Home;
