import React from 'react';
import { useParams } from 'react-router-dom';
import { Helmet } from 'react-helmet';
import {
  Box, Container, Grid, Link as MuiLink,
  Typography
} from '@mui/material';
import { motion } from 'framer-motion';
import PageContainer from '../components/PageContainer';
import projects from '../data/projects';
import '../css/App.css';
import '../css/Animation.css';

const ProjectDetail = function () {
  const { id } = useParams();

  const project = projects.find((proj) => proj.id.toString() === id);

  if (!project) {
    return (
      <PageContainer>
        <Typography variant="h6" align="center" alignContent="center">
          Project not found
        </Typography>
      </PageContainer>
    );
  }

  const { title, description, content, additional_link, thumbnail } = project;

  return (
    <PageContainer title={title}>
      <Helmet>
        <title>{title}</title>
        <meta name="description" content={description} />
        <meta property="og:title" content={title} />
        <meta property="og:description" content={description} />
        <meta property="og:image" content={thumbnail} />
        <meta name="twitter:card" content="summary_large_image" />
        <meta name="twitter:title" content={title} />
        <meta name="twitter:description" content={description} />
        <meta name="twitter:image" content={thumbnail} />
      </Helmet>
      <Container>
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 0.5 }}
        >
          <Typography variant="h4" gutterBottom>
            {title}
          </Typography>
          <Grid container spacing={4}>
            <Grid item xs={12}>
              <Box
                sx={{
                  display: 'flex',
                  flexDirection: 'column',
                  alignItems: 'flex-start',
                  backgroundColor: 'background.paper',
                  paddingBottom: 4,
                  // p: 3,
                  // borderRadius: 2,
                  // boxShadow: 2,
                }}
              >
                <Box
                  component="img"
                  src={thumbnail}
                  alt={title}

                  sx={{
                    alignSelf: 'center',
                    maxWidth: '100%',
                    height: 'auto',
                    mb: 2,
                    borderRadius: '2%',
                  }}
                />
                <Typography variant="caption" gutterBottom>
                  {description}
                </Typography>
                <Typography variant="body1">{content}</Typography>
                {additional_link && <MuiLink
                  href={additional_link}
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  External link
                </MuiLink>}
              </Box>
            </Grid>
          </Grid>
        </motion.div>
      </Container>
    </PageContainer>
  );
};

export default ProjectDetail;
